import Vue from 'vue'
import Router from 'vue-router'

const Home = () =>
    import ('../views/home/Home')
const Product = () =>
    import ('../views/product/Product')
//产品 页面的二级路由
const CIP = () =>
    import ('../views/product/cip/CIP')
const CDP = () =>
    import ('../views/product/cdp/CDP')
const M3 = () =>
    import ('../views/product/m3/M3')


const Solutions = () =>
    import ('../views/solutions/Solutions')
//案例的二级页面
    const HR = () =>
    import ('../views/solutions/hr/HR')
      const Detail = () =>
      import ('../views/solutions/hr/detail/Detail')
    // const Solutions = () =>
    // import ('../views/solutions/Solutions')





const News = () =>
    import ('../views/news/News')
const NewsDetail = () =>
    import ('../views/newsdetail/NewsDetail')

const About = () =>
    import ('../views/about/About')

//关于 页面的二级路由    
const CompanyProfile = () =>
    import ('../views/about/companyprofile/CompanyProfile')
const ContactUs = () =>
    import ('../views/about/contactus/ContactUs')
const BecomePartner = () =>
    import ('../views/about/becomepartner/BecomePartner')


const Feedback = () =>
    import ('../views/feedback/Feedback')

const TrySeeyon = () =>
    import ('../views/tryseeyon/TrySeeyon')

Vue.use(Router)

 const routes = [{
         path: '',
         redirect: '/home'
     },
     {
         path:'/home',
         component: Home,
        //全局导航守卫
         meta:{
           title:'Home'
           },

     },
     //产品页面
     {
         path: '/product',
         component: Product,
         meta:{
            title:'Product'
            },
        children:[
            {
                path:'CDP',
                component: CDP,
            },
            {
                path:'CIP',
                component: CIP,
            },
            {
                path:'M3',
                component: M3,
            }
        ]
     },
     {
         path: '/solutions',
         component: Solutions,
         meta:{
            title:'Solutions'
            },
         children:[
                {
                    path:'detail',
                    component:Detail,
                },
                {
                 path:'hr',
                 component:HR,
               
               },
            //  {
                //  path:'/SM',
                //  component:SM,
            //  },
            //  {
                //  path:'/GO',
                //  component:GO,
            //  },
            //  {
                //  path:'/DM',
                //  component:DM,
            //  }
         ]
     },
     {
         path: '/news',
         component: News,
         meta:{
            title:'News'
            },
     },
    {
        path: '/newsdetail',
        component: NewsDetail,
    },
 // 关于页面路由
     {
         path:'/about',
         component:About,
         meta:{
            title:'About'
        },
         children:[
          {
            path:'CompanyProfile',
            component: CompanyProfile,
          },
          {
              path:'ContactUs',
              component: ContactUs, 
           },
           {
             path:'BecomePartner',
             component: BecomePartner,
           },
          ]
     },

      //反馈页面路由
      {
        path: '/feedback',
        component: Feedback,
        meta:{
            title:'Feedback'
        },
    },
      //try seeyon
      {
        path: '/tryseeyon',
        component: TrySeeyon,
        // meta:{
        //     title:'TrySeeyon'
        // },
    }
  ]
const router = new Router({
    routes,
    mode: 'history'
})

 //全局导航守卫
router.beforeEach((to, from, next) => { 
    //从from跳转到to
    document.title = to.matched[0].meta.title
    next()
  })
export default router