import Vue from 'vue';
import VueI18n from 'vue-i18n';

Vue.use(VueI18n);

const messages = {
    Chinese: require('./Chinese.json'),
    English: require('./English.json'),
};

const i18n = new VueI18n({
    locale: localStorage.getItem('seeyonweb-lang') || 'English',
    fallbackLocale: 'English',
    messages,
});

export default i18n